/* Wrapper */

.wrapper {
    position: relative;

    > .inner {
        // @include padding(5em, 5em);
        padding: 5em 5em 2.5em 5em;
        max-width: 100%;
        width: _size(inner-width);

        @include breakpoint('<=xlarge') {
            padding: 4em 4em 2em 4em;
        }

        @include breakpoint('<=large') {
            width: 100%;
        }

        @include breakpoint('<=small') {
            padding: 3em 2em 1em 2em;
        }
    }

    &.alt {
        background-color: _palette(bg-alt);
    }

    &.style1 {
        background-color: _palette(accent1);
    }

    &.style1-alt {
        background-color: _palette(accent1-alt);
    }

    &.style2 {
        background-color: _palette(accent2);
    }

    &.style2-alt {
        background-color: _palette(accent2-alt);
    }

    &.style3 {
        background-color: _palette(accent3);
    }

    &.style3-alt {
        background-color: _palette(accent3-alt);
    }

    &.fullscreen {
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 100vh;

        body.is-ie & {
            height: 100vh;
        }

        @include breakpoint('<=large') {
            min-height: calc(100vh - 2.5em);

            body.is-ie & {
                height: calc(100vh - 2.5em);
            }
        }

        @include breakpoint('<=small') {
            padding: 2em 0;
            min-height: 0;

            body.is-ie & {
                height: auto;
            }
        }
    }

    &.fade-up {
        > .inner {
            transform: translateY(0);
            transition: opacity #{_duration(activation)} ease, transform #{_duration(activation)} ease;
            opacity: 1;
        }

        &.inactive,
        body.is-preload & {
            > .inner {
                opacity: 0;
                transform: translateY(1em);
            }
        }
    }

    &.fade-down {
        > .inner {
            transform: translateY(0);
            transition: opacity #{_duration(activation)} ease, transform #{_duration(activation)} ease;
            opacity: 1;
        }

        &.inactive,
        body.is-preload & {
            > .inner {
                opacity: 0;
                transform: translateY(-1em);
            }
        }
    }

    &.fade {
        > .inner {
            transition: opacity #{_duration(activation)} ease;
            opacity: 1;
        }

        &.inactive,
        body.is-preload & {
            > .inner {
                opacity: 0;
            }
        }
    }
}
